.infoBox {
  display: inline-block;
  width: 10em;
}

.infoBox .spec {
  display: block;
}

.spec {
  padding: 0.2em 0;
  vertical-align: middle;
  min-height: 1.2em;
}

.draws {
  display: inline-block;
  width: 6em;
}

@media (max-width: 850px) {
  .draws {
    display: block;
    margin: auto;
  }
}
